html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom right, white 0%, #195190c7 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

body {
  padding-top: 20px;
  user-select: none;
}

.react-calculator {
    position: relative;
    margin: 0 auto;
    width: 320px;
    background-color: #292f36;  
    -webkit-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
-moz-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);

}
 

button {
  outline: none;
  border: 0;
  padding: 16px;
  font-size: 20px;
  line-height: 1;
  background-color: #19519052;
  color: #ffce5c;
  transition: all 300ms;
  border: 1px solid #0001;
-webkit-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
-moz-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
}

button:hover {
  color: #00a3f5;
  border: 1px solid #ffce5c22;
transition: all 0.5s ease;
transform: scale(1.08); /* (108% zoom)*/
-moz-transition: all 0.5s ease;
-webkit-transition: all 0.5s ease;
-ms-transition: all 0.5s ease;
-o-transition: all 0.5s ease;
}

.center{
  text-align: center !important;
}
.result {
  position: relative;
  width: 320px;
  height: 80px;
  padding: 8px;
  overflow: hidden;
  background-color: rgba(19, 19, 19, .64);
  border: 1px solid gray;
  font-size: 32px;
  line-height: 2;
  color: #60d63c;
  text-align: right;
  direction: rtl;
  white-space: nowrap;
  box-sizing: border-box;
  -webkit-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
-moz-box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
box-shadow: -1px 2px 5px 2px rgba(0,0,0,0.75);
}

.functions button, .math-operations button {
  display: inline-block;
  width: 80px;
  height: 80px;
  color: #d63c6b
}

.functions button:hover, 
.math-operations button:hover {
  color: #b696a0;
}

.numbers {
  width: 240px;
  float: left;
}

.numbers button {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  float: left;
}

.numbers button:last-child {
  width: 100%;
}

.functions {
  float: left;
  width: 240px;
}
